import { Component, Host, State, h } from '@stencil/core';
import {
    LimelChipCustomEvent,
    LimelChipSetCustomEvent,
} from '@limetech/lime-elements';
import { MenuItem } from '../../menu/menu.types';
import { Chip } from '../chip.types';
import { ListSeparator } from '../../list/list-item.types';

const MENU_ITEMS: Array<MenuItem | ListSeparator> = [
    {
        text: 'Email',
        secondaryText: 'kia@lime.tech',
        icon: 'email_sign',
        value: 1,
    },
    {
        text: 'Direct phone',
        secondaryText: '+46 987 654 321',
        icon: 'phone',
        value: 2,
    },
];

/**
 * Input chip set with menu items is provided
 * When an item is selected from the ellipsis menu, the `onMenuItemSelected` event
 * will be emitted, reflecting the `value` of the selected item.
 *
 * :::note
 * When `removable={true}` or if there are menu items, the "remove button" on the
 * chip will be hidden, as it is automatically added as the last item in the ellipsis
 * menu. This ensures a consistent user interface where all actions related to a chip,
 * including removal, are located within the ellipsis menu.
 *
 * Clicking the remove button will emit the same `onRemove` event.
 * :::
 */
@Component({
    tag: 'limel-example-chip-set-input-type-with-menu-items',
    shadow: true,
})
export class ChipSetInputTypeWithMenuItemsExample {
    @State()
    private selectedItem: MenuItem | ListSeparator = null;

    @State()
    private value: Chip[] = [
        {
            id: 1,
            text: 'Erich Maria Remarque',
            removable: true,
            icon: {
                name: 'bookmark',
                color: 'var(--lime-red)',
            },
            menuItems: MENU_ITEMS,
            value: {
                text: 'Erich Maria Remarque',
                value: 1,
                icon: {
                    name: 'bookmark',
                    color: 'var(--lime-red)',
                },
                actions: MENU_ITEMS,
            },
        },
        {
            id: 2,
            text: 'Ernest Hemingway',
            removable: true,
            icon: {
                name: 'books',
                color: 'var(--lime-magenta)',
            },
            value: {
                text: 'Ernest Hemingway',
                value: 2,
                icon: {
                    name: 'books',
                    color: 'var(--lime-magenta)',
                },
            },
        },
    ];

    public render() {
        return (
            <Host onMenuItemSelected={this.onMenuItemSelected}>
                <limel-chip-set
                    type="input"
                    inputType="text"
                    label="Favorite authors"
                    helperText="Enter your favorite authors' names. Each author will be displayed as a chip with an icon and actions."
                    value={this.value}
                    onChange={this.handleChange}
                    onInteract={this.handleInteraction}
                />
                <limel-example-value
                    label="Selected item"
                    value={this.selectedItem}
                />
            </Host>
        );
    }

    private handleInteraction = (event: CustomEvent<Chip>) => {
        this.selectedItem = { ...event.detail };
    };

    private handleChange = (event: LimelChipSetCustomEvent<Chip[]>) => {
        this.value = event.detail;
        this.selectedItem = null;
    };

    private onMenuItemSelected = (event: LimelChipCustomEvent<MenuItem>) => {
        event.stopPropagation();

        this.selectedItem = { ...event.detail };
    };
}
